import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Popup from "../../components/Popup";
import axios from "axios";
import music from "../../assets/message.mp3";
import notify from "../../assets/starttune.mp3";
import { getAllcatalogue } from "../../store/catalogueSlice";
import io from "socket.io-client";
import Modal from "../../components/Modal/Modal";
import ReactLoading from "react-loading";
import BidderLotComponent from "../../components/reuseable/BidderLotComponent";

const socket = io.connect(process.env.REACT_APP_SERVER_URL);
const Bidders = () => {
  const [show, setShow] = useState(false);
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const dispatch = useDispatch();
  const { auction_data } = useSelector((state) => state.auction);
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);
  const [amountBid, setAmountbid] = useState([]);
  const [manualbidIndex, setManualbidindex] = useState("");
  const [checkedItemName, setCheckedItemName] = useState("");
  const [checkedItemid, setCheckedItemid] = useState("");
  const [activebroker, setActivebroker] = useState();
  const [list, setList] = useState([]);
  const [rawlist, setrawList] = useState([]);
  const [buttonLoading, setbuttonLoading] = useState(false);
  const [disabledButtons, setDisabledButtons] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(null);
  const [activeUsers, setActiveUsers] = useState(0);
  const [visible, setVisible] = useState(false);
  const [showPopup, setShowPopup] = useState(true);
  const [loading, setLoading] = useState(false);
  const [bidloading, setbidLoading] = useState(false);
  const [manualmode, setManualmode] = useState(false);


  const fetchData = async () => {
    try {
      const response = await axios.get(`${API_URL}/getselectbroker`);
      const { auction, current_brokers, name, id } = response.data[0];

      if (auction === 0) {
        setShowPopup(true);
      } else {
        setShowPopup(false);
      }

      await Promise.all([
        setCheckedItemName(name),
        setCheckedItemid(id),
        setActivebroker(current_brokers),
      ]);

      socket.emit("user", current_brokers);
      // socket.emit("user_toggle", current_brokers); need

      const userToggleListener = (userId) => {
        setVisible(userId === users.link_id);
      };

      socket.on("user_toggle", userToggleListener);

      // Cleanup function
      return () => {
        socket.off("users", current_brokers);
        // Remove the userToggleListener when the component unmounts
        socket.off("user_toggle", userToggleListener);
      };
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    let isMounted = true;
    fetchData();
    socket.on("checkname", (data) => {
      if (isMounted) {
        setCheckedItemName(data);
      }
    });
    socket.on("users", (data) => {
    
      if (isMounted) {
        const filteredData = data.filter((item) => item.sold_status === 0);
        setList(data);
        setrawList(data);
      }
    });
    const handleActiveUsers = (count) => {
      setActiveUsers(count);
    };
    socket.on("activeUsers", handleActiveUsers);
    return () => {
      isMounted = false;
  
      socket.off("activeUsers", handleActiveUsers);
      // socket.off("users")
    };
  }, [socket]);

  // new method

  const [secondsToAdd] = useState(60); // Number of seconds to add to current time

  const [endTime, setEndTime] = useState(null); // Initialize endTime as null
  const [currentTime, setCurrentTime] = useState(new Date());
  const [remainingSeconds, setRemainingSeconds] = useState(secondsToAdd);
  const [result, setResult] = useState(0);

  useEffect(() => {
    socket.on("users", (data) => {
        setList(data);
    });
    
    // Listen for the 'endTimeUpdated' event from the server
    socket.on("endTimeUpdated", (data) => {
      setEndTime(new Date(data.endTime).getTime());
    });

    return () => {
      socket.off("users");
    };
  }, []);
  useEffect(() => {
   
    const cancelTokenSource = axios.CancelToken.source();

    const fetchEndTime = async () => {
      try {
        const response = await axios.get(
          "http://68.183.81.28:3009/api/getEndTime", {
            cancelToken: cancelTokenSource.token
          }
        ); // Replace with your API endpoint
        const fetchedEndTime = response.data.endTime;
        if (fetchedEndTime) {
          setEndTime(new Date(Number(fetchedEndTime)).getTime());
        }
      } catch (error) {
        console.error("Error fetching endTime:", error);
      }
    };

    const fetchInitialList = async () => {
      try {
        const response = await axios.get(
          "http://68.183.81.28:3009/catalougedata", {
            cancelToken: cancelTokenSource.token
          }
        ); // Replace with your API endpoint
        const ress = response.data;
        if (ress) {
        
          setList(ress);
          
        }
      } catch (error) {
        console.error("Errors fetching endTime:", error);
      }
    };


    fetchInitialList();
    fetchEndTime();
    return () => {
      cancelTokenSource.cancel("Component unmounted");
    };
  },[]);

  useEffect(() => {
    
    if (endTime) {
      const interval = setInterval(() => {
        const now = new Date();
        setCurrentTime(now);

        const remainingTime = Math.floor((endTime - now.getTime()) / 1000);

        if (remainingTime <= 0) {
          clearInterval(interval); // Clear interval when remaining time reaches zero
          setRemainingSeconds(0); // Set remaining seconds to zero
        } else {
          setRemainingSeconds(remainingTime);
          setResult(Math.ceil(remainingTime / 60)); // Calculate and set the result using Math.ceil
        }
      }, 1000); // Update every 1 second

      return () => clearInterval(interval); // Clean up the interval when component unmounts
    }
  }, [endTime]);

  

  const countdownDisplay = Array.from(
    { length: remainingSeconds },
    (_, index) => (remainingSeconds - index) % 60 || 60
  );
  const firstCountdownResult = countdownDisplay[0];

  const objectToShow = list ? list[result - 1] : {};
  const prevList = list ? list[result ] : {};

  //manual bid inpput data get
  
  const handleInputChange = (event, index) => {
    const { name, value } = event.target;
    setAmountbid((prevState) => {
      const updatedState = [...prevState];
      updatedState[index] = { ...updatedState[index], [name]: value };
      return updatedState;
    });
    setManualbidindex(index);
    // console.log(manualbidIndex);
  };

  //bidder modal
  const openLot = async () => {
    await axios
      .get(`${API_URL}/getselectbroker`)
      .then((response) => {
        socket.emit("user", response.data[0].current_brokers);
      })
      .catch((error) => {
        console.log(error);
      });
    dispatch(getAllcatalogue);
    setShow(true);
  };

  //manuall bid data insert

  const addBid = async (el, index) => {
    const mxbid = el.maximum_bid;
    const percent = mxbid * 0.5;
    const eligiblebid = mxbid + percent;
    const bidValue = amountBid[index]?.name;
  
    if (!bidValue) {
      setManualmode(false);
      return;
    }
  
    if (mxbid !== 0 && bidValue < mxbid) {
      alert("Bid value should be greater than or equal to the maximum bid.");
      return;
    }
  
    if (bidValue > eligiblebid && mxbid !== 0) {
      alert("You can only bid up to 50% of the maximum bid.");
      return;
    }
    setbidLoading(true);
    const bidsToUpdate = {
      id: el.id,
      bid: bidValue,
      sold_status: Number(bidValue)>Number(el.price)?1:el.sold_status,
      buyer: users.link_id,
    };
  
    try {
      await axios.put(`${API_URL}/manualbid`, bidsToUpdate);
      socket.emit("user", activebroker);
      await socket.on("user"(data=>{
         setList(data)
      }))
    } catch (error) {
      console.log(error);
    }
    setManualmode(false);
    setbidLoading(false);
   
  };
  


  const handleKeyPress = (el,event,index) => {
    if (event.key === 'Enter') {
      addBid(el, index)
    }
  };
  //sharebid
  
    

  //bid update
  const handleInc = async (el, index) => {
    setDisabledButtons((prevDisabledButtons) => [
      ...prevDisabledButtons,
      index,
    ]);
    setbidLoading(true);
    const audio = new Audio(music);

    const updatedData = list.map((obj, idx) => {
      if (obj.id === el.id) {
        return {
          ...obj,
          maximum_bid: obj.maximum_bid + 1,
          buyer: users.link_id,
          key: idx,
          sold_status: Number(obj.maximum_bid+1)>Number(obj.price)?1:obj.sold_status,
        };
      }
      return { ...obj, key: idx };
    });

    const filteredData = updatedData.filter((item) => item.id === el.id);
    try {
      await axios.put(`${API_URL}/updateauction`, filteredData);
      socket.emit("user", activebroker);
    } catch (error) {
      console.log(error);
    } finally {
      setList(updatedData);
      audio.play();
      setbuttonLoading(false);
      setbidLoading(false);
      setDisabledButtons((prevDisabledButtons) =>
        prevDisabledButtons.filter((btnIndex) => btnIndex !== index)
      );
    }
  };
  const filteredData = rawlist.filter((item) => item.buyer === users.link_id);
  const formatTime = (time) => {
    const minutes = Math.floor(time / 60)
      .toString()
      .padStart(2, "0");
    const seconds = (time % 60).toString().padStart(2, "0");
    return `${minutes}:${seconds}`;
  };
  if (endTime === null || list === null) {
    // Data is still being fetched, so return a loading indicator or null
    return <div
    style={{
      height: "100vh",
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    <ReactLoading
      type={"spin"}
      color={"#007bff"}
      height={50}
      width={50}
    />
    <div
      style={{
        marginTop: "20px",
        fontSize: "16px",
        fontWeight: "bold",
      }}
    >
      Verifying Data...
    </div>
  </div>;
  }
  return (
    <>
      {bidloading ? (
        <>
          <div className="overlay">
            <div className="spinner-container">
              <div className="spinner"></div>
              <span>Verifying data...</span>
            </div>
          </div>
        </>
      ) : (
        ""
      )}

      {loading && users.type !== "" ? (
        <>
          <div
            style={{
              height: "100vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <ReactLoading
              type={"spin"}
              color={"#007bff"}
              height={50}
              width={50}
            />
            <div
              style={{
                marginTop: "20px",
                fontSize: "16px",
                fontWeight: "bold",
              }}
            >
              Verifying Data...
            </div>
          </div>
        </>
      ) : (
        <>
       
       <Modal
            title="My Lot"
            onClose={() => setShow(false)}
            show={show}
            filteredData={
           filteredData
            }
          ></Modal>
          <div className="section-head sec-h">
            <p className="br-n">={checkedItemName}=</p>
            <div className="row newc">
              <div className="col-7 col-md-9 card-btn leftCard">
                <h2>
                  Auction Date:{" "}
                  {auction_data.length === 0 ? "" : auction_data[0].date}{" "}
                </h2>
                <p>
                  SALE NO - #
                  {auction_data.length === 0 ? "" : auction_data[0].sale_no}{" "}
               
                </p>
                <p>
               
                  PARTICIPATE - #
                   {activeUsers-1}{" "}
                </p>
                <p>
                  Season - #
                  {auction_data.length === 0 ? "" : auction_data[0].season}{" "}
                </p>
                <p>
                  CURRENT BROKER :{" "}
                  <span className="c_brokers">{checkedItemName}</span>{" "}
                </p>

                <div className=" row margin-left">
                  <button
                    type="button"
                    onClick={openLot}
                    className="col-5 buttonGroup btn btn-success my-btn"
                  >
                    My Lot
                  </button>
                </div>
              </div>
              <div className="col-5 col-md-3 align-items-center justify-content-center mt-5 rCard">
                <div className="col-12">
                  <div className="card">
                    <div className="row rightCard">
                      <div className=" ">
                        <div className="col-md-12 count-t">
                          <h2 className="text-center mh2">
                          {formatTime(firstCountdownResult?firstCountdownResult:0)}
                          </h2>
                        </div>
                        <div className="col-md-12 name-t">
                          <h2 className="mh2 text-center">
                            {users.short_name}
                          </h2>
                        </div>
                      </div>
                    </div>
                    <div>
                      <div className="col-md-12 name-t mh3">
                        <h4 className=" text-center">
                          Total : {list.length} Lot
                        </h4>
                       
                      </div>
                      <div className="col-md-12 name-t mh3">
                      <h4 className=" text-center">
                          Remainig Lot : {result?result:''} 
                        </h4>
                       
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="card p-3">
      <div className="row">
        <div className="col-6 col-md-3 d-flex justify-content-start align-items-center">
        <span style={{ backgroundColor: 'white',color:'black', padding: '5px', fontSize: '12px' }}>PLZ BID</span>
          <span style={{ backgroundColor: '#6f6fc4',color:'white', padding: '5px', fontSize: '12px' }}>HBH</span>
        </div>
        <div className="col-6 col-md-9 d-flex justify-content-end" style={{paddingRight:'20px'}}>
          <span style={{ backgroundColor: 'black',color:'white', padding: '5px', fontSize: '12px' }}>{formatTime(firstCountdownResult?firstCountdownResult:0)}</span>
 
        </div>
      </div>
    </div>
          {remainingSeconds === 0 &&  users.type === "BIDDER" ? (
            <>
           
              <Popup />
            </>
          ) : (
            <>
           
              <div className="table-container con">
                <div className="row g-1 bid-table">
                  <table id="auction" className="table table-bordered">
                    <thead>
                      <tr>
                        <th scope="col">
                          Lot <br />
                          Invoice
                        </th>
                        <th scope="col">
                          Factory
                          <br />
                          Grade
                        </th>
                        <th scope="col">
                          Price
                          <br />
                          Qty
                        </th>
                        <th scope="col">
                          Highest
                          <br /> Buyer
                        </th>

                        <th scope="col">Bid</th>
                        
                        <th scope="col">Action</th>
                      </tr>
                    </thead>
                    <tbody>
        
                    {objectToShow && (
          <BidderLotComponent
            handleKeyPress={handleKeyPress}
            uId={users.link_id}
            sl={''}
            time={''}
            key={objectToShow.id}
            el={objectToShow}
            index={objectToShow.id}
            setManualmode={setManualmode}
            setSelectedIndex={setSelectedIndex}
            users={users}
            formatTime={''}
            manualmode={manualmode}
            selectedIndex={selectedIndex}
            handleInputChange={handleInputChange}
            addBid={addBid}
            handleInc={handleInc}
            disabledButtons={disabledButtons}
          />
        )}
                      
                    </tbody>
                  </table>
                </div>
                <div className="">
                <div className="row">
              {prevList?(
                   <>
                   <div className="col-12 col-md-12">
                    <div className="card">
                      <div className={prevList.sold_status===0?"rem-card-x":"rem-card-y"}>
                        <div className="row">
                          <div className="col-8">
                            <div className="col-12">
                              <p className="rem-text">Lot - ({prevList.lot}) {prevList.factory_name} </p>
                            </div>
                            <div className="col-12">
                              <p className="rem-text">{prevList.buyer_name}({prevList.maximum_bid - prevList.price})</p>
                            </div>
                          </div>
                          <div className={`col-4 d-flex align-items-center justify-content-center ${prevList.sold_status === 0 ? 'btcls' : 'btctik'}`}>
 
                            <div className="row">
                              <div className="col-12 ">
                                <i className={prevList.sold_status===0?"bi bi-x-circle ":"bi bi-check2-circle"}></i>
                              </div>
                            
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                   </>
                  ):('')}
                </div>
              </div>
              </div>
              
            </>
          )}
        </>
      )}
    </>
  );
};

export default Bidders;
