import React from "react";
import {
  FaHandPaper,
  FaRegWindowClose,
  FaRegCheckSquare,
} from "react-icons/fa";
const BidderLotComponent = ({
  handleKeyPress,
  
  index,
  time,
  el,
  users,

  manualmode,
  selectedIndex,
  handleInputChange,
  addBid,
  handleInc,
  disabledButtons,
  setSelectedIndex,
  setManualmode,
}) => {

  return (
    <>
    <tr>
      <td className="text-center">
        {el.lot}
        <br />
        {el.invoice}
      </td>
      <td className="text-center">
        {el.factory_name}
        <br/>
        {el.grade}
        {/* <br />
        ({el.grade}) Rating: <span style={{ color: 'purple' }}>({el.lirating_name})</span>
<span style={{ color: 'navy' }}>({el.lrating_name})</span> */}
      </td>
      <td className="text-center">
        {el.price}
        <br />
        {`(${el.pkgs})`}
      </td>
      
<td className="text-center">
{el.maximum_bid !== 0 &&(
  <div className="bidd_name">
              <p className="highest">{el.maximum_bid}</p>
              <span className={el.buyer == users.link_id ? "hname" : "rname"}>
             ({el.maximum_bid - el.price})
              </span>
            </div>
)}
      
           
            <br />
</td>
      <td className="text-center">
        {manualmode === true && selectedIndex === index ? (
          <div className="mn-bid col-12">
            <input
              className="form-control m_bid"
              autoComplete="off"
              type="number"
              id={`name${el.id}`}
              name="name"
              value={el.name}
              onKeyPress={(event) => handleKeyPress(el,event, index)}
              onChange={(e) => handleInputChange(e, index)}
            />
          </div>
        ) : el.maximum_bid === 0 ? (
          <div className="mn-bid col-12">
            {/* <h5 className="card-price">Max.{el.maximum_bid}</h5> */}
            <input
              className="form-control m_bid"
              autoComplete="off"
              type="number"
              id={`name${el.id}`}
              name="name"
              value={el.name}
              onChange={(e) => handleInputChange(e, index)}
            />
          </div>
        ) : (
          <>
           
          </>
        )}
      </td>
 
{/* <td className="text-center"> */}
{/* {Number(el.pkgs) > 15 && el.buyer !== 0 && el.buyer !== uId ? (
  <i
    key={el.id}
    onClick={() => shareBid(el.id)}
    className="bi bi-share"
    style={{ backgroundColor: 'blue', color: 'white', fontSize: '14px' }}
  ></i>
) : null} */}

{/* {Number(el.pkgs) > 15 && el.s_buyer !== 0 && el.buyer !== uId && el.maximum_bid !== 0 && (
  <div className="bidd_name">
    <p className="highest">{el.maximum_bid}</p>
    <span className="sname">
      {el.s_buyer_name}({el.maximum_bid - el.price})
    </span>
  </div>
)} */}



{/* {el.open_share === 0 ? (
            <button key={el.id} onClick={() => openShareBid(el.id)} type="">
              open share
            </button>
          ) : null} */}
{/* </td> */}
      <td className="text-center" >
        {el.maximum_bid === 0 ? (
          <button
            key={el.id + 10}
            disabled={users.type === "BIDDER" ? false : true}
            onClick={() => addBid(el, index)}
            className="btn btn-danger square-circle"
          >
            <FaHandPaper className="bid" />
          </button>
        ) : (
          <div className="col-12 ">
            <div className="bidcont mb-2 d-flex justify-content-center">
            
                  <button
                    key={el.id + 11}
                    disabled={disabledButtons.includes(index)}
                    onClick={() => handleInc(el, index)}
                    className=" btn-danger square-circle bidbtn"
                  >
                    +1
                  </button>
                  <button
                    key={el.id}
                    onClick={() => {
                      if (manualmode === true && selectedIndex === index) {
                        addBid(el, index);
                      } else {
                        setManualmode(true);
                        setSelectedIndex(index);
                      }
                    }}
                    className=" btn-primary square-circle bidbtn"
                    style={{ fontSize: '10px' }}
                  >
                    <FaHandPaper className="bid" />
                  </button>
                
              
            </div>
          </div>
        )}
      </td>
    </tr>
    </>
  );
};

export default BidderLotComponent;
